@import "../../../../utils/extends";
@import "../../../../utils/mixins";
@import "../../../../utils/vars";
@import "../../../Contact-form/Form";

.container--contact {
  display: flex;
  flex-direction: column;
  padding-bottom: 100px;
}

.contact .section__title {
  &::before {
    @extend %icon;
    background-color: $main-light-color;
  }
}

.content__left {
  font-size: 24px;
  line-height: 1.5em;

  .our-email {
    @extend %no-margin;
    margin-bottom: 30px;
  }

  a {
    color: $main-dark-color;
    text-decoration: none;

    &:hover {
      color: $main-accent-color;
      transition: 0.4s;
    }
  }
}

.phones__list {
  @extend %list;
}

.form {
  textarea {
    width: 100%;
    border: none;
    text-align: center;
    text-transform: uppercase;
    background-color: $main-bg-color;
    color: $input-color;
    font-family: Ubuntu, sans-serif;
    font-size: 14px;
    font-weight: 500;
    box-sizing: border-box;
    resize: none;
    padding: 20px;

    &::placeholder {
      font-size: 14px;
      font-weight: 500;
      color: $placeholder-color;
    }

    &:focus {
      outline-color: $main-accent-color;
    }
  }
}

@media (max-width: 1023px) {
  .contact .container {
    width: 768px;
  }
}

@media (max-width: 800px) {
  .contact .container {
    width: 640px;
  }
}

@media (max-width: 679px) {
  .contact .container {
    width: 480px;
  }

  .content {
    flex-flow: row wrap;
    justify-content: center;
  }
}

@media (min-width: 400px) and (max-width: 500px) {
  .contact .container {
    width: 380px;
  }
}

@media (min-width: 320px) and (max-width: 399px) {
  .contact .container {
    width: 280px;

    .section__title {
      font-size: 40px;
    }
  }
}
